<template>
  <!-- 编辑表单对话框 -->
  <EditForm ref="editFormRef" @refresh="refreshData"/>
  
  <!-- 对接资料对话框 -->
  <el-dialog
    v-model="apiDocsVisible"
    title="对接资料"
    width="600px"
    :close-on-click-modal="false"
  >
    <div v-if="currentMerchant" class="api-docs-content">
      <!-- 对接信息 -->
      <el-card class="api-info-card" shadow="never">
        <template #header>
          <div class="card-header">
            <span>对接信息</span>
            <el-button 
              type="primary" 
              size="small" 
              @click="copyAllApiInfo"
              :icon="DocumentCopy"
            >
              一键复制全部
            </el-button>
          </div>
        </template>
        
        <!-- 完整对接信息文本区域 -->
        <div class="api-info-textarea">
          <el-input
            v-model="apiInfoText"
            type="textarea"
            :rows="12"
            readonly
            placeholder="对接信息加载中..."
            class="api-info-text"
          />
        </div>
        
      </el-card>
    </div>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="apiDocsVisible = false">关闭</el-button>
      </div>
    </template>
  </el-dialog>
  
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-row :gutter="20">
        <el-col :span="6" v-if="!isAgent">
          <el-form-item label="代理商">
            <el-select v-model="searchForm.agent_id" placeholder="选择代理商" clearable filterable>
              <el-option
                v-for="agent in agentList"
                :key="agent.id"
                :label="agent.agent_name"
                :value="agent.id"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="商户名称">
            <el-input 
              v-model="searchForm.merchant_name" 
              placeholder="按商户名称搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
              <el-option label="全部" :value="''" />
              <el-option label="启用" :value="1" />
              <el-option label="禁用" :value="0" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 搜索按钮和重置按钮 -->
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button type="primary" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、删除按钮 -->
    <el-button :icon="Refresh" @click="refreshDataWithCircuitStatus" color="#40485b"/>
    <el-button type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete('/api/v1/admin/merchant/delete')">删除</el-button>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;" 
        @selection-change="handleSelectionChange">
      <!-- 表格列定义 -->
      <el-table-column type="selection" width="40"/>
      <el-table-column prop="agent.agent_name" label="代理商" align="center" min-width="150" v-if="!isAgent"/>
      <el-table-column prop="merchant_name" label="商户名称" align="center" min-width="200"/>
      <el-table-column prop="username" label="账号" align="center" min-width="150"/>

      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="90">
        <template #default="{ row }">
          <el-switch 
            v-model="row.status" 
            :active-value="1" 
            :inactive-value="0" 
            @change="toggleSwitch('/api/v1/admin/merchant/switch', row.id)"
          />
        </template>
      </el-table-column>

      <!-- 熔断状态列 -->
      <el-table-column label="熔断状态" align="center" min-width="120">
        <template #default="{ row }">
          <el-tag 
            v-if="row.circuit_status?.is_circuit_open" 
            type="danger" 
            size="small"
          >
            熔断中 ({{ formatRemainTime(row.circuit_status.remain_seconds) }})
          </el-tag>
          <el-tag v-else type="success" size="small">正常</el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="150"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="400">
        <template #default="scope">
          <el-button size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button size="small" type="info" @click="openApiDocs(scope.row)">对接资料</el-button>
          <el-button size="small" type="warning" @click="handleResetApiKey(scope.row)">重置密钥</el-button>
          <el-button 
            size="small" 
            type="danger" 
            :disabled="!scope.row.circuit_status?.is_circuit_open"
            @click="handleClearCircuit(scope.row)"
          >
            解除熔断
          </el-button>
          <el-button size="small" type="danger" @click="handleDelete('/api/v1/admin/merchant/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search, DocumentCopy } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent, computed } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入编辑
const EditForm = defineAsyncComponent(() => import('./edit.vue'));
// 导入编辑页面
const editFormRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log(row);
  if (editFormRef.value) {
    editFormRef.value.open({ ...row }); // 传递 `row` 作为参数
  } else {
    console.error("EditForm 组件未正确挂载！");
  }
};

// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange: originalHandleSizeChange,
  handleCurrentChange: originalHandleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/merchant/list");

// 获取商户熔断状态（提前定义）
const getCircuitStatus = async (row: any) => {
  try {
    const res = await request({
      url: `/api/v1/admin/merchant/circuit-status/${row.id}`,
      method: 'GET'
    });
    if (res.status) {
      row.circuit_status = res.data;
    }
  } catch (error) {
    console.error('获取熔断状态失败:', error);
    row.circuit_status = { is_circuit_open: false, remain_seconds: 0 };
  }
};

// 包装分页处理函数，在分页变化后加载熔断状态
const handleSizeChange = async (size: number) => {
  await originalHandleSizeChange(size);
  if (data.value.data && data.value.data.length > 0) {
    await Promise.all(data.value.data.map((row: any) => getCircuitStatus(row)));
  }
};

const handleCurrentChange = async (page: number) => {
  await originalHandleCurrentChange(page);
  if (data.value.data && data.value.data.length > 0) {
    await Promise.all(data.value.data.map((row: any) => getCircuitStatus(row)));
  }
};

// 搜索表单数据
const searchForm = ref({
  agent_id: '',
  merchant_name: '',
  contact_name: '',
  contact_phone: '',
  status: ''
});

// 代理商列表
const agentList = ref([]);

// 获取用户信息判断是否是代理商
const userInfo = ref<any>({});
const isAgent = computed(() => {
  return userInfo.value?.is_agent === true;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取代理商列表（仅管理员需要）
const getAgentList = async () => {
  if (isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/merchant/agent-list',
      method: 'GET'
    });
    if (res.status) {
      agentList.value = res.data;
    }
  } catch (error) {
    console.error('获取代理商列表失败:', error);
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshDataWithCircuitStatus();
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    agent_id: '',
    merchant_name: '',
    contact_name: '',
    contact_phone: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshDataWithCircuitStatus();
};

// 重置API密钥
const handleResetApiKey = async (row: any) => {
  try {
    await ElMessageBox.confirm('确定要重置该商户的API密钥吗？重置后原密钥将失效！', '警告', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    });
    
    const res = await request({
      url: '/api/v1/admin/merchant/reset-api-key',
      method: 'POST',
      data: { id: row.id }
    });
    
    if (res.status) {
      await ElMessageBox.alert(
        `<div style="word-break: break-all;">
          <p><strong>API Key:</strong><br/>${res.data.api_key}</p>
          <p style="margin-top: 10px;"><strong>API Secret:</strong><br/>${res.data.api_secret}</p>
        </div>`,
        '新的API密钥',
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: '我已复制',
        }
      );
      // 刷新列表数据，更新显示的密钥信息
      await refreshDataWithCircuitStatus();
    } else {
      ElMessage.error(res.msg || '重置失败');
    }
  } catch (error) {
    if (error !== 'cancel') {
      console.error('重置失败:', error);
      ElMessage.error('重置失败');
    }
  }
};

// 格式化剩余时间
const formatRemainTime = (seconds: number) => {
  if (seconds <= 0) return '0秒';
  const minutes = Math.floor(seconds / 60);
  const secs = seconds % 60;
  if (minutes > 0) {
    return `${minutes}分${secs}秒`;
  }
  return `${secs}秒`;
};

// 解除熔断
const handleClearCircuit = async (row: any) => {
  try {
    await ElMessageBox.confirm('确定要解除该商户的熔断状态吗？', '解除熔断', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    });
    
    const res = await request({
      url: '/api/v1/admin/merchant/clear-circuit',
      method: 'POST',
      data: { merchant_id: row.id }
    });
    
    if (res.status) {
      ElMessage.success(res.msg || '熔断已成功解除');
      // 刷新熔断状态
      await getCircuitStatus(row);
      // 刷新列表数据
      await refreshData();
    } else {
      ElMessage.error(res.msg || '解除熔断失败');
    }
  } catch (error) {
    if (error !== 'cancel') {
      console.error('解除熔断失败:', error);
      ElMessage.error('解除熔断失败');
    }
  }
};

// 加载数据后获取每个商户的熔断状态
const loadDataWithCircuitStatus = async () => {
  await loadData();
  // 为每个商户获取熔断状态
  if (data.value.data && data.value.data.length > 0) {
    await Promise.all(data.value.data.map((row: any) => getCircuitStatus(row)));
  }
};

// 刷新数据（包含熔断状态）
const refreshDataWithCircuitStatus = async () => {
  await refreshData();
  // 为每个商户获取熔断状态
  if (data.value.data && data.value.data.length > 0) {
    await Promise.all(data.value.data.map((row: any) => getCircuitStatus(row)));
  }
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getUserInfo();
  await getAgentList();
  await getApiConfig(); // 获取对接配置
  await loadDataWithCircuitStatus();
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api, [row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string) => {
  deleteRows(api);
};

// 对接资料对话框
const apiDocsVisible = ref(false);
const currentMerchant = ref<any>(null);
const apiInfoText = ref('');

// 商户对接配置
const apiConfig = ref({
  callback_ips: '34.92.49.193,34.150.65.167',
  api_gateway: 'https://api.baiyi-pay.com',
  api_docs: 'https://www.baiyi-pay.com/docs.html'
});

// 获取对接配置
const getApiConfig = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/merchant/api-config',
      method: 'GET'
    });
    if (res.status && res.data) {
      apiConfig.value = {
        callback_ips: res.data.callback_ips || apiConfig.value.callback_ips,
        api_gateway: res.data.api_gateway || apiConfig.value.api_gateway,
        api_docs: res.data.api_docs || apiConfig.value.api_docs
      };
    }
  } catch (error) {
    console.error('获取对接配置失败:', error);
    // 失败时使用默认值，不显示错误
  }
};

// 打开对接资料对话框
const openApiDocs = async (merchant: any) => {
  // 每次打开都重新获取最新的商户详情，确保显示最新的密钥信息
  try {
    const res = await request({
      url: `/api/v1/admin/merchant/detail/${merchant.id}`,
      method: 'GET'
    });
    if (res.status) {
      merchant = { ...merchant, ...res.data };
    } else {
      ElMessage.error('获取商户详情失败');
      return;
    }
  } catch (error) {
    console.error('获取商户详情失败:', error);
    ElMessage.error('获取商户详情失败');
    return;
  }
  
  currentMerchant.value = merchant;
  generateApiInfoText();
  apiDocsVisible.value = true;
};

// 生成对接信息文本
const generateApiInfoText = () => {
  if (!currentMerchant.value) return;
  
  const merchant = currentMerchant.value;
  const apiInfo = `=== 百亿支付对接信息 ===

商户名称：${merchant.merchant_name || '未设置'}
商户Key：${merchant.api_key || '未生成'}
商户秘钥：${merchant.api_secret || '未生成'}
回调IP：${apiConfig.value.callback_ips}

API接口信息：
API网关：${apiConfig.value.api_gateway}
API文档：${apiConfig.value.api_docs}

接口说明：
1. 所有接口请求都需要进行签名验证
2. 签名算法：MD5(参数按key排序后拼接 + 商户秘钥)
3. 请求方式：POST
4. 数据格式：JSON
5. 字符编码：UTF-8

回调说明：
- 支付成功后系统会主动回调商户的notify_url
- 回调参数包含订单状态和签名信息
- 商户收到回调后需要返回"success"字符串确认（不区分大小写）
- HTTP状态码必须返回200，响应内容必须为"SUCCESS"

熔断机制：
- 连续超时5次：如果商户回调接口连续5次超时（超过10秒未响应），系统将触发熔断，暂停自动回调5分钟
- 连续非期望响应5次：如果商户回调接口连续5次返回非200状态码或响应内容不是"SUCCESS"，系统将触发熔断，暂停自动回调5分钟
- 熔断恢复：回调成功后自动恢复，或等待5分钟后自动恢复
- 建议：请确保回调接口稳定可用，响应时间控制在10秒以内，避免触发熔断机制

注意事项：
1. 请妥善保管商户Key和商户秘钥
2. 必须使用HTTPS协议进行接口调用
3. 回调地址必须是公网可访问的URL
4. 如有疑问请联系技术支持

生成时间：${new Date().toLocaleString()}`;

  apiInfoText.value = apiInfo;
};

// 一键复制全部对接信息
const copyAllApiInfo = async () => {
  try {
    await navigator.clipboard.writeText(apiInfoText.value);
    ElMessage.success('完整对接信息已复制到剪贴板');
  } catch (err) {
    // 降级方案：使用传统复制方法
    const textarea = document.createElement('textarea');
    textarea.value = apiInfoText.value;
    textarea.style.position = 'fixed';
    textarea.style.opacity = '0';
    document.body.appendChild(textarea);
    textarea.select();
    try {
      document.execCommand('copy');
      ElMessage.success('完整对接信息已复制到剪贴板');
    } catch (e) {
      ElMessage.error('复制失败，请手动复制');
    }
    document.body.removeChild(textarea);
  }
};

</script>

<style scoped>
/* 对接资料对话框样式 */
.api-docs-content {
  max-height: 500px;
  overflow-y: auto;
}

.api-info-card {
  margin-bottom: 16px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  color: #303133;
}

/* 对接信息文本区域样式 */
.api-info-textarea {
  margin-bottom: 16px;
}

.api-info-text :deep(.el-textarea__inner) {
  font-family: 'Courier New', monospace;
  font-size: 13px;
  line-height: 1.5;
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  color: #495057;
  resize: none;
}

.api-info-text :deep(.el-textarea__inner):focus {
  border-color: #409eff;
  box-shadow: 0 0 0 2px rgba(64, 158, 255, 0.1);
}

.dialog-footer {
  text-align: right;
}
</style>

